
.Active-Page{
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    height: 100%;

}

@keyframes slide{
    0%{
        transform: translateX(100%);
    }
    100%{
        transform: translateX(0%);
    }
}


.MainPage{
    border-radius: 50px;
    display: flex;
    width: 100%;
    height:100%;
}

.Active-Page-Content{
    width: 100%;
    height: 100%;
}
